<template>
  <el-form label-width="100px">
    <div class="group">
      <h4>样本选择</h4>
      <el-form-item label="样本名称">
        <sample-select v-on:update:selected="getVars" :init-sample.sync="initSample"></sample-select>
      </el-form-item>
      <el-form-item label="决策时间">
        <datepicker
          :init-dates.sync="decisionDates"
          v-on:update:date="newV => updateDate(newV, 'decisionDates')"
        ></datepicker>
      </el-form-item>
      <el-form-item label="放款时间">
        <datepicker
          :init-dates.sync="loanDates"
          v-on:update:date="newV => updateDate(newV, 'loanDates')"
        ></datepicker>
      </el-form-item>
      <el-form-item label="高级筛选">
        <el-switch v-model="showFiltrate" active-color="#409eff"></el-switch>
      </el-form-item>
      <div v-show="showFiltrate">
        <el-form-item :label="`预筛选${index+1}`" v-for="(one,index) in prefilter" :key="index">
          <var-select
            v-on:update:selected="newV => {one.fieldName = newV[1]; one.initVar = newV}"
            :init-var.sync="one.initVar"
            :var-list.sync="varList"
            style="margin-right: 30px;"
          ></var-select>
          <el-select class="select-compare" v-model.trim="one.comparison" placeholder="请选择">
            <el-option
              v-for="(item, index) in operatorlist"
              :key="index"
              :label="item.text"
              :value="item.value"
            ></el-option>
          </el-select>
          <input class="comm-inp" type="text" v-model.trim="one.value">
          <span class="btn-comm-add" @click="addPrefilter" title="添加预筛选" v-if="index===0"></span>
          <span class="btn-comm-del" @click="delPrefilter(index)" title="删除" v-if="index>0"></span>
        </el-form-item>
      </div>
      <sample-chart ref="sampleChart" class="box-sample-chart"></sample-chart>
    </div>
    <div class="group">
      <h4>样本分析</h4>
      <div v-for="(one, index) in varselectlist" :key="one.id" class="var-select-container">
        <el-form-item :label="`变量 ${index+1} 选择`">
          <var-select
            v-on:update:selected="newV => changeVar(one, newV, index)"
            :init-var.sync="one.initVar"
            :var-list.sync="varList"
          ></var-select>
          <el-popover
            class="el-popper__custom"
            placement="right"
            width="200"
            v-show="one.totalIV"
            trigger="hover"
          >
            <div>
              <div class="el-popover__title">
                <img src="../assets/icon-brow.png" alt>蜂鸟智能
              </div>
              <p>
                <b style="color: #409eff">自动IV值：</b>
                <span>{{one.totalIV}}</span>
              </p>
            </div>
            <img src="../assets/bird-2.gif" alt slot="reference">
          </el-popover>
          <span v-if="index===0" class="btn-comm-add" @click="addVarSelectList" title="添加变量"></span>
          <span v-if="index>0" class="btn-comm-del" @click="delVarSelectList(index)" title="删除变量"></span>
        </el-form-item>
        <el-form-item label="变量分组方式">
          <el-select v-model="one.statisticType" placeholder="请选择变量分组方式">
            <el-option
              v-for="(item, i) in one.sectioncontent"
              :key="i"
              :label="item.text"
              :value="item.value"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
          <el-popover
            class="el-popper__custom"
            placement="right"
            width="400"
            v-show="one.smartRange"
            trigger="hover"
          >
            <div>
              <div class="el-popover__title">
                <img src="../assets/icon-brow.png" alt>蜂鸟智能
              </div>
              <p>
                <b style="color: #409eff">智能分组：</b>
                <span>根据大数据智能分析确定分组</span>
              </p>
              <p
                style="padding: 5px;border: 1px solid #ddd;"
                v-show="one.smartRange"
              >{{one.smartRange}}</p>
            </div>
            <img src="../assets/bird-2.gif" alt slot="reference">
          </el-popover>
        </el-form-item>
        <el-form-item v-show="one.statisticType === 'RATIO_OF_EQUALITY'">
          <el-input class="mini-input" v-model.trim="one.minValue" placeholder="最小值"></el-input>
          <el-input class="mini-input" v-model.trim="one.maxValue" placeholder="最大值"></el-input>
          <el-input class="mini-input" v-model.trim="one.intervalValue" placeholder="间隔"></el-input>
        </el-form-item>
        <el-form-item v-show="one.statisticType === 'FIELD_RANGE'">
          <el-input
            class="comm-textarea"
            type="textarea"
            :rows="5"
            placeholder="请按照以下格式填写：每组最小值和最大值以~分隔，两组之间以英文逗号分隔，如100~300, 300~500"
            v-model.trim="one.rangeListStr"
          ></el-input>
        </el-form-item>
        <el-form-item label="高级分析">
          <el-switch v-model="one.showAssistVar" active-color="#409eff"></el-switch>
        </el-form-item>
        <div v-show="one.showAssistVar">
          <el-form-item v-for="(assistVar, i) in one.assistVarList" :key="i" label="样本选择">
            <sample-select
              v-on:update:selected="newV => getVars(newV, false, assistVar, false)"
              :init-sample.sync="assistVar.initSample"
              style="float:left;margin-right:30px;"
            ></sample-select>
            <span style="float: left;margin-right: 15px;">辅助变量{{ i+1 }}</span>
            <var-select
              v-on:update:selected="newV => {assistVar.fieldName = newV[1]; assistVar.initVar = newV}"
              :init-var.sync="assistVar.initVar"
              :var-list.sync="assistVar.varList"
              style="float:left;margin-right:30px;"
            ></var-select>
            <span class="btn-comm-add" @click="addAssistVar(one.assistVarList)" v-if="i===0"></span>
            <span class="btn-comm-del" @click="delAssistVar(one.assistVarList, i)" v-if="i>0"></span>
          </el-form-item>
        </div>
        <var-chart ref="varChart" class="box-var-chart"></var-chart>
        <!-- <div :id="`box-var-chart${index}`" class="box-var-chart" v-show="one.showVarBar"></div> -->
      </div>
      <el-form-item label="指标类型">
        <el-select v-model.trim="indicator" @change="selectIndicator" placeholder="请选择指标类型">
          <el-option
            v-for="(item, index) in indicatorlist"
            :key="index"
            :label="item.text"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="统计方式" v-show="showloanbefore">
        <el-radio-group v-model.trim="beforeway" style="margin-right:30px;">
          <el-radio v-for="item in countway" :key="item.tag" :label="item.value">{{item.label}}</el-radio>
        </el-radio-group>
        <var-select
          v-show="showavgvar"
          v-on:update:selected="newV => {avgvar = newV[1]; initAvgvar = newV}"
          :init-var.sync="initAvgvar"
          :var-list.sync="varList"
        ></var-select>
        <el-checkbox v-model.trim="ispercent" style="margin-left: 30px;">返回百分比</el-checkbox>
      </el-form-item>
      <el-form-item label="逾期指标" v-show="showloanafter">
        <el-select class="select-loadtag" v-model.trim="overdue" filterable placeholder="请选择逾期指标">
          <el-option
            v-for="(option, index) in overduelist"
            :key="index"
            :label="option.text"
            :value="option.value"
          ></el-option>
        </el-select>
        <span class="var-label">类型</span>
        <el-select class="select-compare" v-model.trim="aftertype" filterable placeholder="请选择">
          <el-option
            v-for="(item, index) in aftertypelist"
            :key="index"
            :label="item.text"
            :value="item.value"
          ></el-option>
        </el-select>
        <span class="var-label">统计值</span>
        <el-radio-group v-model.trim="aftercount" style="margin-right:30px;">
          <el-radio v-for="item in countvalue" :key="item.tag" :label="item.value">{{item.label}}</el-radio>
        </el-radio-group>
      </el-form-item>
    </div>
    <el-form-item>
      <el-button type="primary" @click="calculateAll">开始分析</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import dayjs from 'dayjs';
import sampleSelect from '@/components/sample-select.vue';
import datepicker from '@/components/datepicker';
import varSelect from '@/components/var-select.vue';
import sampleChart from '@/components/sample-chart';
import varChart from '@/components/var-chart';
import mixinsVar from '@/mixins/mixins-var.js';
import mixinsMultivar from '@/mixins/mixins-multivar.js';
import {getVarList} from '@/api/sample.js';
import {saveAnalysis, queryAnalysisDetail, queryVarAutoSplit} from '@/api/analysis.js';
import {queryDecision} from '@/api/decision.js';
import {getBarOption} from '@/assets/scripts/getBarOption.js';
import {
  getSectionContent,
  getOperatorList,
  getOnePrefilter,
  getOneVarSelect,
  getIndicatorlist,
  getCountway,
  getAftertypelist,
  getOverdueList,
  getCountvalue
} from '@/assets/scripts/getSomeSetting.js';
export default {
  name: 'multivariate-form',
  components: {
    sampleSelect,
    datepicker,
    varSelect,
    sampleChart,
    varChart
  },
  props: {
    recordId: {
      type: String
    }
  },
  mixins: [mixinsVar, mixinsMultivar],
  data() {
    return {
      initSample: [],
      decisionDates: [
        dayjs()
          .add(-3, 'month')
          .format('YYYY-MM-DD'),
        dayjs().format('YYYY-MM-DD')
      ], // 决策时间
      loanDates: [
        /*dayjs()
          .add(-3, 'month')
          .format('YYYY-MM-DD'),
        dayjs().format('YYYY-MM-DD')*/
      ], // 放款时间
      showFiltrate: false,
      showloanbefore: false,
      showloanafter: false,
      showSampleChart: false,
      showVarBar: true,
      prefilter: [getOnePrefilter()], // 预筛选默认的一条
      operatorlist: getOperatorList(), // 预筛选的运算条件可选的内容
      varselectlist: [getOneVarSelect() /*, getOneVarSelect()*/],
      sectioncontent: getSectionContent(), // 变量分组方式可选的内容
      indicatorlist: getIndicatorlist(), // 指标类型可选的内容
      countway: getCountway(),
      countvalue: getCountvalue(),
      aftertypelist: getAftertypelist(),
      overduelist: getOverdueList(), // 贷后类型下的逾期指标可选的内容
      varList: [], // 样本对应的所有变量
      indicator: '', // 选择的指标类型
      initAvgvar: [],
      beforeway: 'count', // 用户选择的贷前的统计方式--默认是计数
      avgvar: '', // 用户选择的贷前的统计方式为平均值时选择的变量
      ispercent: true, // 是否返回百分比默认选择
      overdue: [], // 用户选择的贷后类型下的逾期指标
      aftertype: 'peoples', // 用户选择的贷后类型下的类型,默认是金额
      aftercount: 'count' // 用户选择的贷后统计值,默认是逾期量
    };
  },
  computed: {
    showavgvar() {
      // 是否显示用户选择的贷前的统计方式为平均值时的选择变量select,因为默认是计数所以默认不显示此select
      return this.beforeway === 'count' ? false : true;
    }
  },
  created() {
    this.postdata = this.$store.state.analysisResult.multivariate.postdata;
    this.isFromView = !!this.postdata.indexName;
    if (this.isFromView) {
      this.initFields(this.postdata);
      this.getVars([this.rcProductName, this.moduleType, this.samplename], true, {}, true);
    }
  },
  mounted() {
    if (this.isFromView) {
      this.drawSampleChart(this.samplename);
    }
  },
  methods: {
    addPrefilter() {
      this.prefilter.push(getOnePrefilter());
    },
    delPrefilter(index) {
      this.prefilter.splice(index, 1);
    },
    addVarSelectList() {
      if (this.varselectlist.length >= 3) {
        this.$message.warning('最多支持3个变量的分析');
        return;
      }
      this.varselectlist.push(getOneVarSelect());
    },
    delVarSelectList(index) {
      this.varselectlist.splice(index, 1);
    },
    addAssistVar(target = []) {
      if (target.length >= 3) {
        this.$message.warning('辅助变量最多可选3个');
        return;
      }
      target.push({
        rcProductName: '',
        moduleType: '',
        samplename: '',
        fieldName: '',
        varList: []
      });
    },
    delAssistVar(target = [], i) {
      target.splice(i, 1);
    },
    selectIndicator() {
      if (this.indicator === 'BEFORE_LOAN') {
        this.showloanbefore = true;
        this.showloanafter = false;
      } else if (this.indicator === 'AFTER_LOAN') {
        this.showloanbefore = false;
        this.showloanafter = true;
        this.beforeway = 'count';
        this.avgvar = '';
      }
      // 指标类型发生变化，清空数据，隐藏保存导出按钮
      this.twoThead = [];
      this.twoTbody = [];
      this.threeThead = [];
      this.threeTbody = [];
      this.showdatatable = false;
      this.showsave = false;
    },
    getVars(val, isMain = true, target, isInit = false) {
      target = isMain ? this : target;
      target.rcProductName = val[0];
      target.moduleType = val[1];
      target.samplename = val[2];
      target.initSample = val;
      if (isMain && !isInit) {
        target.drawSampleChart(target.samplename);
      }
      getVarList(target.samplename)
        .then(result => {
          if (Array.isArray(result.result)) {
            if (!result.result.length) {
              return;
            }
            target.varList = result.result.map(function({key, vbname, fieldtype, transmittype}) {
              return {
                label: `${vbname}${fieldtype === 'float' ? '（连续）' : '（离散）'}`,
                value: key,
                fieldtype,
                transmittype
              };
            });
            if (isMain && !isInit) {
              this.resetFields();
            }
            if (isMain && isInit) {
              this.initFieldsAboutVar(this.postdata);
              target.$nextTick(() => {
                for (let [i, item] of target.varselectlist.entries()) {
                  target.drawVarChart(item, i);
                }
              });
            }
            if (!isMain && isInit) {
              console.log(target);
            }
            if (!isMain && !isInit) {
              target.initVar = [];
            }
          }
        })
        .catch(err => console.log(err));
    },
    drawSampleChart(samplename) {
      this.$refs.sampleChart.$emit('drawSampleChart', samplename);
    },
    checkForm(isVar = false) {
      if (this.samplename.length === 0) {
        this.$message.warning('请选择样本');
        return;
      }
      if (isVar) {
        return true;
      }
      if (!this.checkVarlist(this.varselectlist, 0)) {
        return;
      }

      if (this.indicator.length === 0) {
        this.$message.warning('请选择指标类型');
        return;
      }
      if (this.indicator === 'BEFORE_LOAN' && this.beforeway === 'avg' && this.avgvar.length === 0) {
        this.$message.warning('请选择平均值对应的变量');
        return;
      }
      if (this.indicator === 'AFTER_LOAN' && this.overdue.length === 0) {
        this.$message.warning('请选择逾期指标');
        return;
      }
      return true;
    },
    checkVarlist(data, init) {
      let len = data.length;
      let result = true;
      //console.log("len====",len);
      for (let i = 0; i < len; i++) {
        if (data[i].fieldName.length === 0) {
          //console.log("======检查变量======");
          this.$message.warning('请选择样本分析中的变量' + (i + 1 + init));
          result = false;
          break;
        }
        if (data[i].statisticType.length === 0) {
          //console.log("======检查分组方式======");
          this.$message.warning('请选择样本分析中的变量' + (i + 1 + init) + '的分组方式');
          result = false;
          break;
        }
        if (data[i].statisticType === 'RATIO_OF_EQUALITY') {
          //console.log("======检查等比分段的最小值、最大值、间隔======");
          if (data[i].minValue.length === 0 || data[i].maxValue.length === 0 || data[i].intervalValue.length === 0) {
            this.$message.warning('请填写变量' + (i + 1 + init) + '的等差分组的最小值、最大值、间隔');
            result = false;
            break;
          }
        } else if (data[i].statisticType === 'FIELD_RANGE') {
          if (!data[i].rangeListStr) {
            this.$message.warning(`请填写变量${i + 1 + init}的自定义分组值`);
            result = false;
            break;
          }
          let str = data[i].rangeListStr;
          let arr = str.split(',');
          data[i].rangeList = [];
          for (let n = 0; n < arr.length; n++) {
            let rangeArr = arr[n].split('~');
            data[i].rangeList.push({
              rangeMin: rangeArr[0] && rangeArr[0].trim(),
              rangeMax: rangeArr[1] && rangeArr[1].trim()
            });
          }
          for (let n = 0; n < data[i].rangeList.length; n++) {
            let range = data[i].rangeList[n] || {};
            let reg = /^(-?\d+)(\.\d+)?$/;
            let rangeMin = range.rangeMin;
            let rangeMax = range.rangeMax;
            if ((rangeMin !== 'null' && !reg.test(rangeMin)) || (rangeMax !== 'null' && !reg.test(rangeMax))) {
              this.$message.warning('变量${i + 1 + int}的自定义分组值格式错误');
              result = false;
              break;
            }
          }
        }
      }
      return result;
    },
    calculateAll() {
      if (!this.checkForm()) {
        return;
      }
      let postdata = this.getPostData();
      postdata.statisticConditionList = [];
      let samplenames = [this.samplename];
      // 生成变量数组及样本数组
      for (let item of this.varselectlist) {
        postdata.statisticConditionList.push({
          fieldName: item.fieldName,
          statisticType: item.statisticType,
          minValue: item.minValue,
          maxValue: item.maxValue,
          intervalValue: item.intervalValue,
          rangeList: item.rangeList
        });
        if (item.showAssistVar) {
          for (let assist of item.assistVarList) {
            if (assist.fieldName) {
              postdata.statisticConditionList.push({
                fieldName: assist.fieldName,
                statisticType: item.statisticType,
                minValue: item.minValue,
                maxValue: item.maxValue,
                intervalValue: item.intervalValue,
                rangeList: item.rangeList
              });
            }
            if (samplenames.indexOf(assist.samplename) < 0) {
              samplenames.push(assist.samplename);
            }
          }
        }
      }
      postdata.indexName = samplenames;
      postdata.loanType = this.indicator;
      if (this.indicator === 'BEFORE_LOAN') {
        // 选择贷前
        if (this.beforeway === 'count') {
          postdata.resultValueType = 'COUNT';
          postdata.resultField = '';
        }
        if (this.beforeway === 'avg') {
          postdata.resultValueType = 'FIELD_AVG';
          postdata.resultField = this.avgvar;
        }
        postdata.resultNeedPercent = this.ispercent;
      }
      if (this.indicator === 'AFTER_LOAN') {
        // 选择贷后
        if (this.aftertype === 'peoples') {
          postdata.resultValueType = 'COUNT';
          postdata.resultField = '';
        }
        if (this.aftertype === 'amount') {
          postdata.resultValueType = 'FIELD_SUM';
          postdata.resultField = 'LoanCapital';
        }
        postdata.resultNeedPercent = this.aftercount === 'rate';
        let overduedata = this.overdue.split('-');
        postdata.afterLoanKPI = overduedata[0];
        postdata.overdueDays = overduedata[1];
      }
      queryDecision(postdata)
        .then(result => {
          if (result.result.originalReport.length === 0) {
            this.$message.warning('暂无数据');
            return;
          }
          if (result.result.originalReport.length > 0) {
            this.$message.success('多变量分析成功');
            postdata.initAvgvar = this.initAvgvar;
            postdata.statisticConditionList = this.varselectlist;
            this.threeTbody = [];
            this.threeThead = [];
            this.twoTbody = [];
            this.twoThead = [];
            const table = this.resolveData(result.result.originalReport);
            if (this.varselectlist.length === 2) {
              //console.log("=====2个变量的分析=====");
              this.showTwoVarsTable = true;
              this.showThreeVarsTable = false;
              this.twoTbody = table.tbody;
              this.twoThead = table.thead;
            } else if (this.varselectlist.length > 2) {
              //console.log("=====3个变量的分析=====");
              this.showTwoVarsTable = false;
              this.showThreeVarsTable = true;
              this.threeTbody = table.tbody;
              this.threeThead = table.thead;
            }
            const file = this.$store.state.analysisResult.file;
            this.$store.commit('RESET_ANALYSISRESULT');
            if (file.id) {
              this.$store.commit('UPDATE_ANALYSISRESULT', {
                key: 'file',
                value: file
              });
            }
            this.$store.commit('UPDATE_ANALYSISRESULT', {
              key: 'multivariate',
              value: {
                postdata: postdata,
                threeThead: this.threeThead,
                threeTbody: this.threeTbody,
                twoThead: this.twoThead,
                twoTbody: this.twoTbody,
                stashThead: [],
                stashTbody: []
              }
            });
            this.$router.push('/multivariate-view');
          }
        })
        .catch(err => console.log(err));
    },
    resolveData(data) {
      console.log(data);
      //console.log("======开始解析2个变量分析的数据=====");
      let thead = [];
      let tbody = [];
      data.forEach((v1, m1) => {
        tbody.push({
          id: m1,
          key: v1.key,
          value: v1.value,
          abnormal: v1.abnormal,
          numerator: v1.numerator,
          denominator: v1.denominator,
          rate: '',
          children: [
            {
              id: `0-${m1 + 1}`,
              x: v1.key,
              y: v1.key,
              key: v1.key,
              value: v1.key,
              abnormal: false,
              numerator: v1.numerator,
              denominator: v1.denominator,
              rate: '',
              children: []
            }
          ]
        });
        v1.children = Array.isArray(v1.children) ? v1.children : [];
        v1.children.forEach((v2, m2) => {
          if (!thead.includes(v2.key)) {
            thead.push(v2.key);
          }
          tbody[m1].children.push({
            id: `${m2 + 1}-${m1 + 1}`,
            x: v1.key,
            y: v2.key,
            key: v2.key,
            value: v2.value === null ? 'N/A' : v2.value,
            abnormal: v2.abnormal,
            numerator: v2.numerator,
            denominator: v2.denominator,
            rate: '',
            children: v2.children
          });
        });
      });
      console.log(tbody);
      console.log(thead);
      return {
        thead,
        tbody
      };
    }
  }
};
</script>

