import {queryVarAutoSplit} from '@/api/analysis.js';
import {getSectionContent} from '@/assets/scripts/getSomeSetting.js';

const mixins = {
  methods: {
    getPostData() {
      let postdata = {};
      postdata.indexName = [this.samplename];
      postdata.createTimeStart = this.decisionDates[0];
      postdata.createTimeEnd = this.decisionDates[1];
      postdata.loanTimeStart = this.loanDates[0];
      postdata.loanTimeEnd = this.loanDates[1];
      postdata.rcProductName = this.rcProductName;
      postdata.moduleType = this.moduleType;

      let prefilter = this.prefilter.filter(function(v) {
        return v.comparison.length > 0 && v.fieldName.length > 0 && v.value.length > 0;
      });
      postdata.filterConditionList = this.showFiltrate ? prefilter : [];

      postdata.statisticConditionList = this.varselectlist;
      return postdata;
    },
    resetFields() {
      this.varselectlist.forEach((v, i) => {
        v.fieldName = '';
        v.statisticType = '';
        v.minValue = '';
        v.maxValue = '';
        v.intervalValue = '';
        v.rangeListStr = '';
        v.smartRange = '';
        v.totalIV = '';
        v.initVar = [];
      });
      this.prefilter.forEach((v, i) => {
        v.fieldName = '';
        v.comparison = '';
        v.value = '';
        v.initVar = [];
      });
      if (this.showavgvar) {
        this.initAvgvar = [];
      }
    },
    initFieldsAboutVar(paramsaved) {
      // 预筛选赋值
      if (paramsaved.filterConditionList && paramsaved.filterConditionList.length) {
        this.showFiltrate = true;
        for (let i = 0; i < paramsaved.filterConditionList.length; i++) {
          const item = paramsaved.filterConditionList[i];
          // 兼容以前数据，以前filterConditionList上不存在initVar
          if (!Array.isArray(item.avgvar)) {
            const theVar = this.varList.find(one => one.value === item.fieldName) || {};
            item.initVar = [theVar.transmittype, theVar.value];
          }
        }
        this.prefilter = paramsaved.filterConditionList;
      }
      // 贷后平均值-变量
      if (Array.isArray(paramsaved.initAvgvar)) {
        this.initAvgvar = paramsaved.initAvgvar;
      }
      // 兼容以前保存数据 item上不存在sectioncontent字段;
      for (let i = 0; i < paramsaved.statisticConditionList.length; i++) {
        const item = paramsaved.statisticConditionList[i];
        if (!(Array.isArray(item.sectioncontent) && item.sectioncontent.length)) {
          item.sectioncontent = getSectionContent();
        }
        if (!Array.isArray(item.initVar)) {
          const theVar = this.varList.find(one => one.value === item.fieldName) || {};
          item.initVar = [theVar.transmittype, theVar.value];
        }
        if (Array.isArray(item.rangeList)) {
          item.rangeListStr = item.rangeList.map(range => `${range.rangeMin}~${range.rangeMax}`).join(', ');
        }
        if (item.totalIV) {
          item.totalIV = Number(item.totalIV).toFixed(2);
        }
        item.assistVarList.forEach(assistVar => {
          assistVar.varList = [];
          if (item.showAssistVar) {
            if (this.initSample.length === 3) {
              this.getVars(assistVar.initSample, false, assistVar, true);
            }
          } else {
            assistVar.initSample = [];
          }
        });
      }
      this.varselectlist = paramsaved.statisticConditionList;
    },
    updateDate(newV, key) {
      this[key] = newV;
      if (this.samplename && this.samplename.length === 0) {
        return;
      }
      if (this.varselectlist[0].fieldName.length === 0) {
        return;
      }
      for (let item of this.varselectlist) {
        this.drawVarChart(item);
      }
    },
    changeVar(target = {}, val = [], index = 0) {
      target.fieldName = val[1];
      target.initVar = val;
      const theVar = this.varList.find(item => item.value === val[1]) || {};
      if (theVar.fieldtype === 'float') {
        queryVarAutoSplit(this.samplename, target.fieldName)
          .then(result => {
            target.smartRange = '';
            target.totalIV = '';
            target.sectioncontent[0].disabled = true;
            if (result.result && Array.isArray(result.result.variableAreaRangeList)) {
              target.rangeList = result.result.variableAreaRangeList;
              let arr = target.rangeList.map(item => {
                return `${item.rangeMin}~${item.rangeMax}`;
              });
              target.smartRange = arr.join(', ');
              target.totalIV = result.result.totalIV.toFixed(2);
              if (target.smartRange.length > 0) {
                target.statisticType = 'SMART_RANGE';
                target.sectioncontent[0].disabled = false;
              }
            }
            this.drawVarChart(target, index);
          })
          .catch(err => {
            console.log(err);
            this.getAutoSplitFail(target, index);
          });
      } else {
        this.getAutoSplitFail(target, index);
      }
    },
    getAutoSplitFail(target, index) {
      if (target.statisticType === 'SMART_RANGE') {
        target.statisticType = '';
      }
      target.sectioncontent[0].disabled = true;
      target.smartRange = '';
      target.totalIV = '';
      this.drawVarChart(target, index);
    },
    drawVarChart(item, i = 0) {
      if (!this.checkForm(true)) {
        this.cansubmit = true;
        return;
      }
      let postdata = this.getPostData();
      postdata.statisticConditionList = [
        {
          fieldName: item.fieldName,
          statisticType: item.statisticType || 'FIELD_GROUP',
          rangeList: item.rangeList || []
        }
      ];
      // 默认贷前
      postdata.loanType = 'BEFORE_LOAN';
      if (this.beforeway === 'count') {
        // 选择计数
        postdata.resultValueType = 'COUNT';
        postdata.resultField = '';
      }
      if (this.beforeway === 'avg') {
        // 选择平均值
        postdata.resultValueType = 'FIELD_AVG';
        postdata.resultField = this.avgvar;
      }
      postdata.resultNeedPercent = this.ispercent;
      let theOne = this.varList.find(item => item.value === item.fieldName) || {};
      let title = theOne.label;
      this.$refs.varChart[i].$emit('drawVarChart', postdata, title);
    }
  }
};

export default mixins;
