<template>
  <el-form label-width="100px">
    <div class="group">
      <h4>样本选择</h4>
      <el-form-item label="样本名称">
        <sample-select v-on:update:selected="getVars" :init-sample.sync="initSample"></sample-select>
      </el-form-item>
      <el-form-item label="决策时间">
        <datepicker
          :init-dates.sync="decisionDates"
          v-on:update:date="newV => updateDate(newV, 'decisionDates')"
        ></datepicker>
      </el-form-item>
      <el-form-item label="放款时间">
        <datepicker
          :init-dates.sync="loanDates"
          v-on:update:date="newV => updateDate(newV, 'loanDates')"
        ></datepicker>
      </el-form-item>
      <el-form-item label="高级筛选">
        <el-switch v-model="showFiltrate" active-color="#409eff"></el-switch>
      </el-form-item>
      <div v-show="showFiltrate">
        <el-form-item :label="`预筛选${index+1}`" v-for="(one,index) in prefilter" :key="index">
          <var-select
            v-on:update:selected="newV => {one.fieldName = newV[1]; one.initVar = newV}"
            :init-var.sync="one.initVar"
            :var-list.sync="varList"
            style="margin-right: 30px;"
          ></var-select>
          <el-select class="select-compare" v-model.trim="one.comparison" placeholder="请选择">
            <el-option
              v-for="(item, index) in operatorlist"
              :key="index"
              :label="item.text"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-input v-model.trim="one.value" style="width: 220px; margin-right: 30px;"></el-input>
          <span class="btn-comm-add" @click.stop="addPrefilter" title="添加预筛选" v-if="index===0"></span>
          <span class="btn-comm-del" @click.stop="delPrefilter(index)" title="删除" v-if="index>0"></span>
        </el-form-item>
      </div>
      <sample-chart ref="sampleChart" class="box-sample-chart"></sample-chart>
    </div>
    <div class="group">
      <h4>样本分析</h4>
      <div v-for="one in varselectlist" :key="one.id">
        <el-form-item label="变量选择">
          <var-select
            v-on:update:selected="newV => changeVar(one, newV)"
            :init-var.sync="one.initVar"
            :var-list.sync="varList"
          ></var-select>
          <el-popover
            class="el-popper__custom"
            placement="right"
            width="200"
            v-show="one.totalIV"
            trigger="hover"
          >
            <div>
              <div class="el-popover__title">
                <img src="../assets/icon-brow.png" alt>蜂鸟智能
              </div>
              <p>
                <b style="color: #409eff">自动IV值：</b>
                <span>{{one.totalIV}}</span>
              </p>
            </div>
            <img src="../assets/bird-2.gif" alt slot="reference">
          </el-popover>
        </el-form-item>
        <el-form-item label="变量分组方式">
          <el-select v-model="one.statisticType" placeholder="请选择变量分组方式">
            <el-option
              v-for="(item, index) in one.sectioncontent"
              :key="index"
              :label="item.text"
              :value="item.value"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
          <el-popover
            class="el-popper__custom"
            placement="right"
            width="400"
            v-show="one.smartRange"
            trigger="hover"
          >
            <div>
              <div class="el-popover__title">
                <img src="../assets/icon-brow.png" alt>蜂鸟智能
              </div>
              <p>
                <b style="color: #409eff">智能分组：</b>
                <span>根据大数据智能分析确定分组</span>
              </p>
              <p
                style="padding: 5px;border: 1px solid #ddd;"
                v-show="one.smartRange"
              >{{one.smartRange}}</p>
            </div>
            <img src="../assets/bird-2.gif" alt slot="reference">
          </el-popover>
        </el-form-item>
        <el-form-item v-show="one.statisticType === 'RATIO_OF_EQUALITY'">
          <el-input class="mini-input" v-model.trim="one.minValue" placeholder="最小值"></el-input>
          <el-input class="mini-input" v-model.trim="one.maxValue" placeholder="最大值"></el-input>
          <el-input class="mini-input" v-model.trim="one.intervalValue" placeholder="间隔"></el-input>
        </el-form-item>
        <el-form-item v-show="one.statisticType === 'FIELD_RANGE'">
          <el-input
            class="comm-textarea"
            type="textarea"
            :rows="5"
            placeholder="请按照以下格式填写：每组最小值和最大值以~分隔，两组之间以英文逗号分隔，如100~300, 300~500"
            v-model.trim="one.rangeListStr"
          ></el-input>
        </el-form-item>
        <var-chart ref="varChart" class="box-var-chart"></var-chart>
      </div>
      <el-form-item label="指标类型">
        <el-select
          v-model.trim="indicator"
          multiple
          placeholder="请选择指标类型"
          @change="selectIndicator"
        >
          <el-option
            v-for="(option, index) in indicatorlist"
            :key="index"
            :label="option.text"
            :value="option.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="统计方式" v-show="showloanbefore">
        <el-radio-group v-model.trim="beforeway" style="margin-right:30px;">
          <el-radio v-for="item in countway" :key="item.tag" :label="item.value">{{item.label}}</el-radio>
        </el-radio-group>
        <var-select
          v-show="showavgvar"
          v-on:update:selected="newV => {avgvar = newV[1]; initAvgvar = newV}"
          :init-var.sync="initAvgvar"
          :var-list.sync="varList"
        ></var-select>
        <el-checkbox v-model.trim="ispercent" style="margin-left: 30px;">返回百分比</el-checkbox>
      </el-form-item>
      <el-form-item label="逾期指标" v-show="showloanafter">
        <el-select
          class="select-loadtag"
          v-model.trim="overdue"
          filterable
          multiple
          placeholder="请选择逾期指标"
        >
          <el-option
            v-for="(option, index) in overduelist"
            :key="index"
            :label="option.text"
            :value="option.value"
          ></el-option>
        </el-select>
        <span class="var-label">类型</span>
        <el-select class="select-compare" v-model.trim="aftertype" filterable placeholder="请选择">
          <el-option
            v-for="(item, index) in aftertypelist"
            :key="index"
            :label="item.text"
            :value="item.value"
          ></el-option>
        </el-select>
        <span class="var-label">统计值</span>
        <el-radio-group v-model.trim="aftercount" style="margin-right:30px;">
          <el-radio v-for="item in countvalue" :key="item.tag" :label="item.value">{{item.label}}</el-radio>
        </el-radio-group>
      </el-form-item>
    </div>
    <el-form-item>
      <el-button type="primary" @click="calculateAll">开始分析</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import dayjs from 'dayjs';
import sampleSelect from '@/components/sample-select.vue';
import datepicker from '@/components/datepicker';
import varSelect from '@/components/var-select.vue';
import sampleChart from '@/components/sample-chart';
import varChart from '@/components/var-chart';
import mixinsVar from '@/mixins/mixins-var.js';
import mixinsUnivar from '@/mixins/mixins-univar.js';
import {getVarList} from '@/api/sample.js';
import {saveAnalysis, queryVarAutoSplit} from '@/api/analysis.js';
import {queryDecision} from '@/api/decision.js';
import {
  getSectionContent,
  getOperatorList,
  getOnePrefilter,
  getOneVarSelect,
  getIndicatorlist,
  getCountway,
  getAftertypelist,
  getOverdueList,
  getCountvalue
} from '@/assets/scripts/getSomeSetting.js';

export default {
  name: 'univariate-form',
  components: {
    sampleSelect,
    datepicker,
    varSelect,
    sampleChart,
    varChart
  },
  props: {
    recordId: {
      type: String
    }
  },
  mixins: [mixinsVar, mixinsUnivar],
  data() {
    return {
      initSample: [],
      decisionDates: [
        dayjs()
          .add(-3, 'month')
          .format('YYYY-MM-DD'),
        dayjs().format('YYYY-MM-DD')
      ], // 决策时间
      loanDates: [
        /*dayjs()
          .add(-3, 'month')
          .format('YYYY-MM-DD'),
        dayjs().format('YYYY-MM-DD')*/
      ], // 放款时间
      showFiltrate: false,
      showloanbefore: false,
      showloanafter: false,
      showVarBar: true,
      prefilter: [getOnePrefilter()], // 预筛选默认的一条
      operatorlist: getOperatorList(), // 预筛选的运算条件可选的内容
      varselectlist: [getOneVarSelect()],
      sectioncontent: getSectionContent(), // 变量分组方式可选的内容
      indicatorlist: getIndicatorlist(), // 指标类型可选的内容
      countway: getCountway(),
      countvalue: getCountvalue(),
      aftertypelist: getAftertypelist(),
      overduelist: getOverdueList(), // 贷后类型下的逾期指标可选的内容
      varList: [], // 样本对应的所有变量
      indicator: [], // 选择的指标类型
      initAvgvar: [],
      beforeway: 'count', // 用户选择的贷前的统计方式--默认是计数
      avgvar: '', // 用户选择的贷前的统计方式为平均值时选择的变量
      ispercent: true, // 是否返回百分比默认选择
      overdue: [], // 用户选择的贷后类型下的逾期指标
      aftertype: 'peoples', // 用户选择的贷后类型下的类型,默认是金额
      aftercount: 'count', // 用户选择的贷后统计值,默认是逾期量
      resultallList: []
    };
  },
  computed: {
    showavgvar() {
      // 是否显示用户选择的贷前的统计方式为平均值时的选择变量select,因为默认是计数所以默认不显示此select
      return this.beforeway === 'count' ? false : true;
    }
  },
  watch: {
    beforeway(cur) {
      if (cur === 'count') {
        this.avgvar = '';
      }
    }
  },
  created() {
    this.resultallList = this.$store.state.analysisResult.univariate;
    this.isFromView = !!(this.resultallList[0] && this.resultallList[0].resultall.length);
    if (this.isFromView) {
      this.postdata = this.initFields(this.resultallList);
      this.getVars([this.rcProductName, this.moduleType, this.samplename], true, {}, true);
    }
  },
  mounted() {
    if (this.isFromView) {
      this.drawSampleChart(this.samplename);
    }
  },
  methods: {
    addPrefilter() {
      this.prefilter.push(getOnePrefilter());
    },
    delPrefilter(index) {
      this.prefilter.splice(index, 1);
    },
    selectIndicator() {
      if (this.indicator.indexOf('BEFORE_LOAN') > -1) {
        this.showloanbefore = true;
      } else {
        this.showloanbefore = false;
      }
      if (this.indicator.indexOf('AFTER_LOAN') > -1) {
        this.showloanafter = true;
      } else {
        this.showloanafter = false;
      }
    },
    getVars(val, isMain = true, target, isInit = false) {
      target = isMain ? this : target;
      target.rcProductName = val[0];
      target.moduleType = val[1];
      target.samplename = val[2];
      target.initSample = val;
      if (isMain && !isInit) {
        target.drawSampleChart(target.samplename);
      }
      getVarList(target.samplename).then(result => {
        if (Array.isArray(result.result)) {
          if (!result.result.length) {
            return;
          }
          target.varList = result.result.map(function({key, vbname, fieldtype, transmittype}) {
            return {
              label: `${vbname}${fieldtype === 'float' ? '（连续）' : '（离散）'}`,
              value: key,
              fieldtype,
              transmittype
            };
          });
          if (isMain && !isInit) {
            this.resetFields();
          }
          if (isMain && isInit) {
            this.initFieldsAboutVar(this.postdata);
            this.$nextTick(() => {
              for (let item of this.varselectlist) {
                this.drawVarChart(item);
              }
            });
          }
        }
      });
    },
    drawSampleChart(samplename) {
      this.$refs.sampleChart.$emit('drawSampleChart', samplename);
    },
    calculateAll() {
      if (!this.checkForm()) {
        return;
      }
      const resultallList = [];
      const promiseArr = [];
      const postdata = this.getPostData();
      if (this.indicator.indexOf('BEFORE_LOAN') > -1) {
        let data = JSON.parse(JSON.stringify(postdata));
        // 选择贷前
        data.loanType = 'BEFORE_LOAN';
        //data.resultNeedPercent = this.ispercent;
        data.resultNeedPercent = false;
        if (this.beforeway === 'count') {
          // 选择计数
          data.resultValueType = 'COUNT';
          data.resultField = '';
        }
        if (this.beforeway === 'avg') {
          // 选择平均值
          data.resultValueType = 'FIELD_AVG';
          data.resultField = this.avgvar;
          data.initAvgvar = this.initAvgvar;
        }
        promiseArr.push(queryDecision(data));
        resultallList.push({
          ispercent: this.ispercent,
          showbar: true,
          postdata: data,
          resultall: []
        });
      }
      if (this.indicator.indexOf('AFTER_LOAN') > -1) {
        for (let item of this.overdue) {
          let data = JSON.parse(JSON.stringify(postdata));
          let overduedata = item.split('-');
          data.afterLoanKPI = overduedata[0];
          data.overdueDays = overduedata[1];
          data.loanType = 'AFTER_LOAN';
          // 选择贷后
          if (this.aftertype === 'peoples') {
            // 类型选择人数
            data.resultValueType = 'COUNT';
            data.resultField = '';
          } else if (this.aftertype === 'amount') {
            // 类型选择金额
            data.resultValueType = 'FIELD_SUM';
            data.resultField = 'LoanCapital';
          }
          if (this.aftercount === 'count') {
            // 统计值选择逾期量
            data.resultNeedPercent = false;
          } else if (this.aftercount === 'rate') {
            // 统计值选择逾期率
            data.resultNeedPercent = true;
          }
          resultallList.push({
            ispercent: false,
            showbar: true,
            postdata: data,
            resultall: []
          });
          promiseArr.push(queryDecision(data));
        }
      }
      Promise.all(promiseArr)
        .then(result => {
          for (let i = 0; i < result.length; i++) {
            let data = result[i];
            resultallList[i].resultall = data.result.originalReport;
          }
          const file = this.$store.state.analysisResult.file;
          this.$store.commit('RESET_ANALYSISRESULT');
          if (file.id) {
            this.$store.commit('UPDATE_ANALYSISRESULT', {
              key: 'file',
              value: file
            });
          }
          this.$store.commit('UPDATE_ANALYSISRESULT', {
            key: 'univariate',
            value: resultallList
          });
          this.$router.push('/univariate-view');
        })
        .catch(err => console.log(err));
    },
    checkForm(isVar = false) {
      if (this.samplename.length === 0) {
        this.$message.warning('请选择样本');
        return;
      }
      if (this.varselectlist[0].fieldName.length === 0) {
        this.$message.warning('请选择样本分析中的变量');
        return;
      }
      if (isVar) {
        return true;
      }
      if (this.varselectlist[0].statisticType.length === 0) {
        this.$message.warning('请选择样本分析中的变量分组方式');
        return;
      }
      if (this.varselectlist[0].statisticType === 'RATIO_OF_EQUALITY') {
        if (
          this.varselectlist[0].minValue.length === 0 ||
          this.varselectlist[0].maxValue.length === 0 ||
          this.varselectlist[0].intervalValue.length === 0
        ) {
          this.$message.warning('请填写等差分组的最小值、最大值、间隔');
          return;
        }
      } else if (this.varselectlist[0].statisticType === 'FIELD_RANGE') {
        if (!this.varselectlist[0].rangeListStr) {
          this.$message.warning('请填写自定义分组');
          return;
        }
        let str = this.varselectlist[0].rangeListStr;
        let arr = str.split(',');
        this.varselectlist[0].rangeList = [];
        for (let i = 0; i < arr.length; i++) {
          let rangeArr = arr[i].split('~');
          this.varselectlist[0].rangeList.push({
            rangeMin: rangeArr[0] && rangeArr[0].trim(),
            rangeMax: rangeArr[1] && rangeArr[1].trim()
          });
        }
        let hasError = false;
        for (let i = 0; i < this.varselectlist[0].rangeList.length; i++) {
          let range = this.varselectlist[0].rangeList[i] || {};
          let reg = /^(-?\d+)(\.\d+)?$/;
          let rangeMin = range.rangeMin;
          let rangeMax = range.rangeMax;
          if ((rangeMin !== 'null' && !reg.test(rangeMin)) || (rangeMax !== 'null' && !reg.test(rangeMax))) {
            hasError = true;
            this.$message.warning('自定义分组格式错误');
            break;
          }
        }
        if (hasError) {
          return;
        }
      }
      if (this.indicator.length === 0) {
        this.$message.warning('请选择指标类型');
        return;
      }
      if (this.indicator.indexOf('BEFORE_LOAN') > -1 && this.beforeway === 'avg' && this.avgvar.length === 0) {
        this.$message.warning('请选择平均值对应的变量');
        return;
      }
      if (this.indicator.indexOf('AFTER_LOAN') > -1 && this.overdue.length === 0) {
        this.$message.warning('请选择逾期指标');
        return;
      }
      return true;
    }
  }
};
</script>

