<template>
  <div class="var-chart-container">
    <div id="var-chart" v-show="showChart"></div>
  </div>
</template>

<script>
import {getBarOption} from '@/assets/scripts/getBarOption.js';
import {queryDecision} from '@/api/decision.js';

export default {
  name: 'var-chart',
  data() {
    return {
      showChart: false
    };
  },
  mounted() {
    this.$on('drawVarChart', (postdata, title) => {
      this.drawFunc(postdata, title);
    });
  },
  methods: {
    drawFunc(postdata, title) {
      this.showChart = true;
      this.getData(postdata)
        .then(result => {
          if (!this.chart) {
            const elem = this.$el.querySelector('#var-chart');
            this.chart = echarts.init(elem);
          }
          const option = this.getOption(result.result.originalReport, title);
          this.chart.setOption(option);
        })
        .catch(err => {
          console.log(err);
          this.showChart = false;
        });
    },
    getData(postdata) {
      return queryDecision(postdata);
    },
    getOption(data = [], title) {
      if (!data.length) {
        return;
      }
      let datalist = data.map(function({key, value}) {
        return {
          key: key,
          value: parseFloat(value)
        };
      });
      let option = getBarOption(title, datalist);
      option.xAxis[0].name = '变量取值';
      option.yAxis[0].name = '计数';
      if (option.series[0].data.every(v => isNaN(v))) {
        console.log('======有非数字值存在=====');
        this.showChart = false;
        return;
      }
      if (option.series[0].data.length < 10) {
        option.series[0].barWidth = 20;
      }
      if (option.series[0].data.length > 30) {
        option.series[0].barWidth = 10;
      }
      return option;
    }
  }
};
</script>

<style scoped>
#var-chart {
  position: relative;
  width: 100%;
  height: 100%;
}
</style>
