const mixins = {
  methods: {
    initFields(paramsaved) {
      if (!paramsaved.rcProductName) {
        return;
      }
      this.rcProductName = paramsaved.rcProductName;
      this.moduleType = String(paramsaved.moduleType);
      this.samplename = Array.isArray(paramsaved.indexName) ? paramsaved.indexName[0] : paramsaved.indexName;
      if (paramsaved.createTimeStart && paramsaved.createTimeEnd) {
        this.decisionDates = [paramsaved.createTimeStart, paramsaved.createTimeEnd];
      }
      if (paramsaved.loanTimeStart && paramsaved.loanTimeEnd) {
        this.loanDates = [paramsaved.loanTimeStart, paramsaved.loanTimeEnd];
      }
      if (paramsaved.loanType === 'BEFORE_LOAN') {
        // 贷前
        this.indicator = 'BEFORE_LOAN';
        this.showloanbefore = true;
        this.showloanafter = false;
        if (paramsaved.resultValueType === 'COUNT') {
          this.beforeway = 'count';
        }
        if (paramsaved.resultValueType === 'FIELD_AVG') {
          this.beforeway = 'avg';
          this.avgvar = paramsaved.resultField;
        }
        this.ispercent = paramsaved.resultNeedPercent;
      }
      // 贷后数据处理
      if (paramsaved.loanType === 'AFTER_LOAN') {
        //贷后
        this.indicator = 'AFTER_LOAN';
        this.showloanafter = true;
        this.showloanbefore = false;
        this.overdue = paramsaved.afterLoanKPI + '-' + paramsaved.overdueDays;
        if (paramsaved.resultValueType === 'COUNT') {
          this.aftertype = 'peoples';
        }
        if (paramsaved.resultValueType === 'FIELD_SUM') {
          this.aftertype = 'amount';
        }
        if (paramsaved.resultNeedPercent === true) {
          this.aftercount = 'rate';
        } else {
          this.aftercount = 'count';
        }
      }
      return paramsaved;
    }
  }
};

export default mixins;
