<template>
  <div class="variate">
    <el-tabs type="border-card" v-model="varType">
      <el-tab-pane name="univariate">
        <span class="tabs-title" slot="label">单变量分析
          <el-popover placement="right" width="500" trigger="hover">
            <div>
              <p>选择样本中单个变量指标，展示该变量指标的贷前总数和贷后总逾期数据情况。</p>
              <p>
                <b style="color:#409eff">样本：</b>作为单变量决策分析对象，可选择不同产品不同模块下数据作为分析样本，例如选择卡卡贷产品授信模块下的全国版2017年11月份的所有数据作为分析大的样本，设置对某决策和放款时间段内数据进行分析，其中预筛选作为高级筛选条件存在，达到缩小样本范围，更精准的进行决策分析的目的。
              </p>
              <p>
                <b style="color:#409eff">变量：</b>可以选择一个变量指标，分析该变量指标的贷前和贷后数据情况，同时可设置分段或等差分组或自定义分组方式显示统计结果，便于观察数据情况。
              </p>
              <p>
                <b style="color:#409eff">指标：</b>可同时分析贷前贷后作为分析指标情况，贷前的统计方式有计数和平均值统计，贷后主要针对逾期各指标进行统计。
              </p>
            </div>
            <i class="el-icon-question" slot="reference"></i>
          </el-popover>
        </span>
        <univariate :record-id.sync="uniRecordId"></univariate>
      </el-tab-pane>
      <el-tab-pane name="multivariate">
        <span class="tabs-title" slot="label">多变量分析
          <el-popover placement="right" width="500" trigger="hover">
            <div>
              <p>选择样本中多个变量指标，展示这多个指标影响下的贷前总数据和贷后总逾期数据情况。</p>
              <p>
                <b style="color:#409eff">样本：</b>作为多变量决策分析对象，可选择不同产品不同模块下数据作为分析样本，例如选择卡卡贷产品授信模块下的全国版2017年11月份的所有数据作为分析大的样本，设置对某决策和放款时间段内数据进行分析，其中预筛选作为高级筛选条件存在，达到缩小样本范围，更精准的进行决策分析的目的。
              </p>
              <p>
                <b style="color:#409eff">变量：</b>可以选择多个变量指标，分析这多个变量指标的贷前和贷后数据情况来辅助决策，同时可设置分段或等差分组或自定义分组方式显示统计结果有助于更方便观察数据情况。
              </p>
              <p>
                <b style="color:#409eff">指标：</b>选择贷前贷后作为分析指标，贷前的统计方式有计数和平均值统计，贷后主要针对逾期各指标进行统计。
              </p>
            </div>
            <i class="el-icon-question" slot="reference"></i>
          </el-popover>
        </span>
        <multivariate :record-id.sync="multiRecordId"></multivariate>
      </el-tab-pane>
      <el-tab-pane name="ruleanalysis">
        <span class="tabs-title" slot="label">规则分析
          <el-popover placement="right" width="500" trigger="hover">
            <div>
              <p>在满足一定规则的设定下，展示所选择变量的贷前贷后总指标情况。</p>
            </div>
            <i class="el-icon-question" slot="reference"></i>
          </el-popover>
        </span>
        <ruleanalysis :record-id.sync="ruleRecordId"></ruleanalysis>
      </el-tab-pane>
    </el-tabs>
    <router-link to="/record-list" class="el-button el-button--text btn-link">查看历史分析记录</router-link>
  </div>
</template>

<script>
import univariate from '@/components/univariate.vue';
import multivariate from '@/components/multivariate.vue';
import ruleanalysis from '@/components/ruleanalysis.vue';

export default {
  name: 'variate',
  components: {
    univariate,
    multivariate,
    ruleanalysis
  },
  data() {
    return {
      uniRecordId: '',
      multiRecordId: '',
      ruleRecordId: ''
    };
  },
  computed: {
    varType: {
      get: function() {
        return this.$store.state.varType;
      },
      set: function(newV) {
        if (newV === this.varType) {
          return;
        }
        this.$store.commit('UPDATE_VAR_TYPE', newV);
      }
    }
  },
  created() {
    const recordId = this.$route.query.recordid;
    if (this.varType === 'univariate') {
      this.uniRecordId = recordId;
    } else if (this.varType === 'multivariate') {
      this.multiRecordId = recordId;
    } else if (this.varType === 'ruleanalysis') {
      this.ruleRecordId = recordId;
    }
  },
  methods: {}
};
</script>

<style scoped>
.variate {
  position: relative;
}
.el-tabs {
  margin: 20px 15px 0;
}
.btn-link {
  position: absolute;
  right: 30px;
  top: 5px;
}
.tabs-title {
  font-size: 16px;
}
</style>
